







import { Component, Vue } from 'vue-property-decorator';

@Component({
  components: {},
})
export default class Header extends Vue {
  switchGlobalMenuDrawer() {
    this.$store.commit('switchGlobalMenuDrawer');
  }

  get title() {
    return this.$route.meta.title === '' ? '' : ` / ${this.$route.meta.title}`;
  }
}
